.notebookHead {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .notebookHeadRight {
    display: flex;
    color: $theme-color-katonic;
    align-items: center;
    a {
      color: $theme-color-katonic;
    }
  }
}

.environment {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 20px;
  position: relative;
}

[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

[type="radio"] + img {
  cursor: pointer;
  width: 160px;
  height: 75px;
  object-fit: contain;
  border: 2px solid #f3f3f3;
  border-radius: 5px;
  background-color: #fff;
}

.freeLab:checked + img {
  border: 2px solid #5c31ff;
}

.upgradeLab + img {
  background-color: #f3f3f3;
}

.hide {
  display: none;
}

.imgLab:hover + .hide {
  display: block;
  color: black;
  border: 1px solid lightgray;
  border-radius: 4px;
  padding: 5px;
  position: absolute;
  margin-left: 10px;
  width: 200px;
  top: 60px;
  background-color: rgb(218, 214, 214);
  color: black;
  font-size: 13px;
  font-weight: 400;
}

.workspaceCreateBtn {
  border: none;
  background-color: white;
  color: #5c31ff;
  margin-right: 20px;
  font-size: 15px;
  font-weight: 800;
  border: 1px solid #eee;
  border-radius: 5px;
  padding: 0.3rem;
}

.workspaceCreateBtn > span {
  font-size: 18px;
  margin-right: 5px;
  font-weight: 800;
}

.workspaceCreateBtn:hover,
.refreshButton:hover {
  background-color: rgba(92, 49, 255, 0.08);
}

.hideMessage{
  display: none;
}

.workspaceCreateBtn:hover + .hideMessage{
  display: block;
  color: black;
  border: 1px solid lightgray;
  border-radius: 4px;
  padding: 5px;
  position: absolute;
  top: 75px;
  right: 67px;
  margin-left: 10px;
  width: 230px;
  background-color: rgb(218, 214, 214);
  color: black;
  font-size: 13px;
  font-weight: 400;
}

.disabledBtn {
  background-color: rgb(218, 214, 214);
}

.disabledBtn:hover {
  background-color: rgb(218, 214, 214);
}

.refreshButton {
  border: none;
  background-color: white;
  color: #5c31ff;
  font-size: 15px;
  font-weight: 800;
  padding: 0.6rem;
}

.newTable {
  border: 1px solid lightgray;
  width: 50vw;
  // height: 32vh;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  position: relative;
  left: 15vw;
  margin-bottom: 20px;
}

#tableLeft {
  // height: 80%;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  border-right: 1px solid lightgray;
}

#tableRight {
  width: 60%;
  position: relative;
  padding-left: 20px;
}

.innerSection {
  width: 30%;
  min-width: fit-content;
  padding: 10px;
}

.rightTop {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.dataLabel {
  margin: 0;
  margin-top: 15px;
  margin-bottom: 5px;
}

.dataBox {
  padding: 5px 10px;
  border: 1px solid lightgray;
  border-radius: 3px;
  width: fit-content;
  margin-bottom: 25px;
}

.runningBtn {
  // margin: 10px;
  margin-left: 0;
  margin-top: 35px;
  padding: 5px 35px;
  margin-bottom: 20px;
  border-style: none;
  background-color: rgb(219, 255, 219);
  color: green;
  font-size: 12px;
  border-radius: 5px;
}

.stoppedBtn {
  margin-left: 0;
  margin-top: 35px;
  padding: 5px 35px;
  margin-bottom: 10px;
  border-style: none;
  background-color: rgb(229, 188, 188);
  color: red;
  font-size: 12px;
  border-radius: 5px;
}

.processingBtn {
  margin-left: 0;
  margin-top: 35px;
  padding: 5px 35px;
  margin-bottom: 10px;
  border-style: none;
  background-color: rgba(92, 49, 255, 0.08);
  color: #5c31ff;
  font-size: 12px;
  border-radius: 5px;
}

.connectBtn {
  background-color: #5c31ff;
  color: white;
  width: 100px;
  padding: 5px;
  margin: 20px 20px 20px 0;
}

.stopBtn {
  background-color: black;
  color: white;
  min-width: 100px;
  margin: 20px 0px 20px 20px;
}

.btnContainer {
  width: 100%;
  // display: flex;
  // flex-direction: row-reverse;
  // justify-content: left;
  padding-left: 20px;
  padding-top: 10px;
  padding: 0;
}

.imgContainer > img {
  height: 150px;
  margin-top: 20px;
}

.deleteBtnContainer {
  display: flex;
  justify-content: right;
  margin-top: 15px;
}

.deleteIcon {
  margin: 10px;
  margin-top: 15px;
  padding: 5px 20px;
  border-style: none;
  color: red;
  cursor: pointer;
  font-size: 21px;
  border-radius: 5px;
}

.introPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 20vw;
  width: 45%;
  margin-top: 70px;
}

.introPage > h3 {
  font-size: 27px;
  margin-bottom: 20px;
  color: gray;
}

.introPage > p {
  font-size: 18px;
  margin-left: 70px;
  text-align: center;
}

.introPage > p > span > a {
  color: #5c31ff;
}

.statusIndicator button {
  font-size: 15px;
}

.workspaceImg {
  margin-top: 40px;
  margin-bottom: 40px;
  height:300px;
  width: 300px;
}