:root {
  --toastify-color-progress-info: #5c31ff !important;
  --toastify-icon-color-info: #5c31ff !important;
}

* {
  padding: 0;
  margin: 0;
}

.progressBar {
  background: #5c31ff;
}

/* Popup style */
.popup-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}
 
.box {
  position: relative;
  width: 40%;
  height: fit-content;
  max-height: 70vh;
  background: #fff;
  border-radius: 10px;
  border: 1px solid #999;
  overflow: auto;
}
 
.close-icon {
  content: 'x';
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0;
  width: 25px;
  height: 25px;
  color: #5c31ff;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}

.popup-box-content {
  text-align: center;
}

.popup-box-btns {
  width: 100%;
}

.popup-icon {
  font-size: 70px;
}

.popup-btn {
  width: 50%;
  background: none;
  border: none;
  font-size: 18px;
  padding: 7px 28px;
  margin-top: 20px;
  text-align: center;
  background-color: rgb(233, 230, 230);
}

.popup-btn-delete {
  background-color: red;
  border-radius: 0 0 5px 0;
  color: #fff;
}

.popup-info {
  display: flex;
  padding: 0 10px;
}

.popup-content-left {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-content-right {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-right: 20px;
}

#dashboard-nav-item {
  scroll-margin-top: 80px;
}
.appStoreIcon {
  background: url("../../appIcon.svg") no-repeat;
  background-size: cover;
  display: inline-block;
  height: 32px;
  width: 32px;
  margin-bottom: 5px;
  // color: #3a3a3a;
}

