.app {
  // width: 93.8vw;
  margin: 0;
  padding: 0;
  margin-top: -6vh;
  // margin-left: -4vw;
  overflow-x: hidden;
}

.appHead {
  display: flex;
  overflow: hidden;
  padding: 25px;
  padding-top: 40px;
  width: 100%;
  margin-bottom: 40px;
  // background-color: rgb(241, 238, 238);
}

.collectionHead {
  width: 100%;
  margin: 0;
}

.collectionHeadRight {
  padding-left: 50px;
}

.appHeadLeft {
  margin-left: 25px;
  width: 85%;
}

.smallAppContainer {
  outline: none;
  background-color: transparent;
  border: none;
}

.appHeadRight {
  margin-top: 15px;
  width: 15%;
  padding-right: 30vw;
}

.appContent {
  display: flex;
  width: 90vw;
  margin-left: 2.5vw;
  padding: 0;
}

.appLeftScroll {
  width: 12.5%;
  margin-right: 1.5vh;
  height: 75vh;
  overflow-y: scroll;
}

.appLeftScroll::-webkit-scrollbar {
  width: 4px;
  display: flex;
}

.appLeftScroll::-webkit-scrollbar-thumb {
  width: 4px;
  background-color: rgb(230, 230, 230);
  border-radius: 3px;
}

.appLeftScroll::-webkit-scrollbar-thumb:hover {
  background-color: rgb(200, 200, 200);
}

.appLeft {
  width: 12.5%;
  height: 90vh;
  margin-right: 1.5vh;
}

.appRight {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
}

.appTop {
  margin-top: -20px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: start;
}

.noAppTop {
  display: none;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}

.appBottom {
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.appCard {
  position: relative;
  cursor: pointer;
  width: 30%;
  min-width: 300px;
  border: 1px solid rgb(238, 237, 237);
  overflow: hidden;
  margin: 20px;
  border-radius: 15px;
  height: 280px;
  transition: all 0.2s;
}

.appCard:hover {
  transform: translateY(-2px);
}

.content {
  color: black;
  position: relative;
  padding: 20px;
}

.itemHeading {
  margin-top: 70px;
  font-size: 1.7rem;
}

.itemHeadingWhite {
  margin-top: 70px;
  font-size: 1.7rem;
  color: white;
}

.belowHeading {
  font-size: 1.7rem;
  margin-top: -20px;
}

.belowHeadingWhite {
  font-size: 1.7rem;
  margin-top: -20px;
  color: white;
}

.contentTag {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: black;
  top: 180px;
  font-size: 17px;
  border: 2px solid black;
  font-weight: 500;
  padding: 10px;
  border-radius: 8px;
}

.contentTagWhite {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: white;
  top: 180px;
  font-size: 17px;
  border: 2px solid white;
  font-weight: 500;
  padding: 10px;
  border-radius: 8px;
}

.contentTag:hover {
  color: inherit;
}

.contentTagWhite:hover {
  color: white;
}

.content > a > span {
  padding-bottom: 5px;
  padding-left: 5px;
}

.appImg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}

.appHeading {
  font-size: 30px;
}

.appDesc {
  font-size: 17px;
  font-weight: 500;
}

.appInfo {
  padding: 15px;
  width: 100%;
}

.inputBox {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  width: 25vw;
  border: 1px solid white;
  padding: 5px;
  background-color: white;
}

.inputBox:hover {
  border: 1px solid #5c31ff;
}

.inputBox:focus {
  border-color: #5c31ff;
  box-shadow: 0 0 1px #5c31ff;
  outline: 0 none;
}

.inputBox > i {
  width: 5%;
  padding: 3px;
}

.appSearch {
  width: 95%;
  border: none;
  outline: none;
}

