.smallAppList {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
}

.smallApp {
  cursor: pointer;
  border: 1px solid rgb(238, 237, 237);
  display: flex;
  // flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
  margin: 20px;
  padding: 15px;
  height: 200px;
  width: 22%;
  min-width: 300px;
  background: white;
  transition: all 0.5s;
}

.smallApp:hover,
.smallAppPopup:hover {
  border: 1px solid #5c31ff;
  color: #5c31ff;
}

.smallAppPopup {
  cursor: pointer;
  border: 1px solid rgb(204, 202, 202);
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  margin: 20px;
  padding: 15px;
  height: 200px;
  width: 15%;
  min-width: 250px;
  background: white;
  transition: all 0.5s;
}

.appLogo {
  margin-top: 10px;
  margin-right: 20px;
  margin-bottom: 10px;
  height: 40px;
  width: 40px;
}

.appLogoCategory {
  margin-top: 10px;
  margin-right: 20px;
  margin-bottom: 10px;
  height: 40px;
  width: 40px;
}

.appSmallInfo {
  display: flex;
  flex-direction: column;
  justify-content: left;
  text-align: left;
}

.appSmallInfo > h4 {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 5px;
}

.appSmallInfo > p {
  color: gray;
}

.filterDiv {
  width: 200px;
}

.params {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 20px;
}

.params > label {
  font-size: 16px;
  margin: 0;
}

.params > select {
  padding: 10px;
  border: 1px solid lightgrey;
  outline: none;
  border-radius: 5px;
  width: 270px;
  background-color: #fff;
}

.optionSpan {
  width: 90%;
}

.arrowSpan {
  width: 10%;
}

.arrowDown {
  border: solid black;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.appSectionList {
  list-style: none;
}

.appSectionList > li {
  font-size: 15px;
  padding: 5px;
  cursor: pointer;
}

.appSectionList > li:hover {
  color: #5c31ff;
}

.filteredAppList {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: start;
  flex-wrap: wrap;
}

.collectionPage {
  z-index: -1000;
  width: 94.5vw;
  height: 83vh;
  display: flex;
  flex-direction: column;
  top: -5vh;
  left: 13vh;
  margin: 0;
  overflow: hidden;
  margin-top: -6vh;
  margin-left: -8vh;
  margin-bottom: -5.3vh;
}

.collectionPageTop {
  width: 100%;
}

.collectionPageBottom {
  position: relative;
  height: 77vh;
}

.collectionLeft {
  background: url("https://static.intercomassets.com/ember/assets/images/appstore/collections/super-charge-data-vertical-7eef7a627c0f0578776d3196fac382db.png")
    no-repeat;
  background-size: cover;
  object-fit: contain;
  width: 30%;
  height: 70vh;
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2px;
}

.collectionRight {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 57%;
  max-width: 70%;
  display: flex;
  height: fit-content;
  overflow-y: scroll;
  max-height: 400px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.collectionRight::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.collectionRight {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.collectionImg {
  width: 100%;
  height: 73vh;
}

.leftInfo {
  width: 20%;
  color: white;
  position: absolute;
  z-index: 2;
  left: 0;
  padding: 40px;
  transform: translateY(-50%);
  top: 45%;
}

.leftInfo > h1 {
  margin-top: 20px;
  font-size: 23px;
  font-weight: 800;
}

.leftInfo > p {
  font-size: 14px;
}

.smallAppCollection {
  border: 1px solid rgb(184, 182, 182);
}

