.experimentsMenu {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .experimentsMenuLeft {

    }

    .experimentsMenuRight {
        display: flex;
        align-items: center;
        .experimentsMenuButton {
            background-color: $theme-color-katonic;
            padding: 0.3rem;
            font-size: 0.9rem;
            color: #fff;
            font-weight: 700;
            border: transparent;
            border-radius: 3px;
            align-self: center;
        }

        .experimentsMenuRightItem {
            cursor: pointer;
            font-size: 0.9rem;
            font-weight: 700;
            margin-left: 0.8rem;
            color: $theme-color-katonic;
            
        }
    }
}

.experimentsSubMenu {
    display: flex;

    h3{
        font-size: 0.9rem;
        font-weight: 700;
        margin-right: 1rem;
    }
    .experimentsSubMenuLine {
        width: 100%;
        color: gray;
    }
}
